import { Component, Input, OnInit } from '@angular/core';
import { GetErrorMessage } from '@sofico-framework/utils';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Component({
  selector: 'sof-get-error-messages',
  template: `
    <sof-alert
      type="danger"
      *ngIf="errorMessage$ | async as msg"
      [isDismissible]="false"
    >
      {{ msg | translate }}
    </sof-alert>
  `,
  styleUrls: ['./get-error-messages.component.scss']
})
export class GetErrorMessagesComponent implements OnInit {
  @Input() tc: string;
  @GetErrorMessage() error$: Observable<string>;
  errorMessage$: Observable<string>;

  ngOnInit(): void {
    this.errorMessage$ = this.error$.pipe(map(error => `${this.tc}.${error}`));
  }
}
